	module.exports = {"data":{"markdownRemark":{"html":"<h3>Day 1</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>1. Introduction</h4>\n<ul>\n<li>Learn a little more in-depth JavaScript programming</li>\n<li>Learn the fundamental concepts of Object Oriented Programming</li>\n<li>Learn the most common packages used in Node Package Manager(NPM)</li>\n<li>Learn how to use Flexbox</li>\n<li>Learn how to utilize Bootstrap</li>\n<li>Installing Visual Studio Code</li>\n<li>Developer Tools in Google Chrome</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>2. Scope and Operators</h4>\n<ul>\n<li>\n<p>Variations of scope</p>\n</li>\n<li>\n<p>Concept of closures</p>\n</li>\n<li>\n<p>The <em>this</em> keyword</p>\n</li>\n<li>\n<p>Spread operators.</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 2</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>3. Document Object Model</h4>\n<ul>\n<li>JavaScript allows for dynamic changes to be made to the DOM</li>\n<li>Retrieving DOM Elements</li>\n<li>Creating DOM Elements</li>\n<li>Event Object</li>\n<li>Switch Statements</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>4. Decision Making</h4>\n<ul>\n<li>\n<p>try…catch</p>\n</li>\n<li>\n<p>Regular Expressions (regex)</p>\n</li>\n<li>\n<p>Throw Error</p>\n</li>\n<li>\n<p>&#x26;&#x26; and || operators for Conditional Statements</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 3</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>5. Object-Oriented Programming in JavaScript</h4>\n<ul>\n<li>Class Building ES5 and Beyond</li>\n<li>Prototype Linking</li>\n<li>Inheritance</li>\n<li>Instance, Instantiation</li>\n<li>Instantiation</li>\n<li>Method v Functions</li>\n<li>Callback Function -> Promises</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>6. Introduction to jQuery</h4>\n<ul>\n<li>\n<p>jQuery History</p>\n</li>\n<li>\n<p>HTML/DOM manipulation</p>\n</li>\n<li>\n<p>CSS manipulation</p>\n</li>\n<li>\n<p>HTML event methods</p>\n</li>\n<li>\n<p>Effects and animations</p>\n</div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 4</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>7. AJAX</h4>\n<ul>\n<li>JSON Objects, Nested Objects</li>\n<li>API’s</li>\n<li>HTTP Request Types</li>\n<li>XMLHttpRequest -> AJAX -> Fetch</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>8. Introduction to Flexbox</h4>\n<ul>\n<li>\n<p>Problems Flexbox Solves</p>\n</li>\n<li>\n<p>Wrapping Flex Items</p>\n</li>\n<li>\n<p>Containers</p>\n</li>\n<li>\n<p>Sort, Order, Spacing</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 5</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>9. Introduction to Bootstrap</h4>\n<ul>\n<li>Problems Bootstrap Solves</li>\n<li>Advantages with Bootstrap</li>\n<li>Animation requires jQuery</li>\n<li>Grid Layout</li>\n<li>Navigation Components</li>\n<li>Wrapping Rows Dynamicly xs sm md lg</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>10. Final Project</h4>\n<h5>Personal Website</h5>\n<ul>\n<li>\n<p>Layout your page using Flexbox or Bootstrap.</p>\n</li>\n<li>\n<p>A header with your name and your job title (ie. Web Developer, Full Stack Developer, etc..)</p>\n</li>\n<li>\n<p>A footer that sticks to the bottom of the screen, commonly referred to as a sticky footer.</p>\n</li>\n<li>\n<p>The footer should contain contact information with an email icon and an email address listed next to it.</p>\n</li>\n<li>\n<p>An image of yourself so everyone knows who you are.</p>\n</li>\n<li>\n<p>Clickable GitHub icon linking to your GitHub.</p>\n</li>\n<li>\n<p>Clickable LinkedIn icon linking to your LinkedIn.</p>\n</li>\n<li>\n<p>Other Professional Social Media (ie. Twitter, Facebook, Instagram, etc..) icons</p>\n</li>\n<li>\n<p>Using jQuery, when your icons are hovered over they should transform in size using CSS to show animation.</p>\n</li>\n<li>\n<p>List your tech-related skills.</p>\n</li>\n<li>\n<p>Dynamically list your GitHub repositories.</p>\n</li>\n<li>\n<p>The repositories should be hyperlinks to the actual repository page on your GitHub account.</p>\n</li>\n<li>\n<p>Your list of repositories should be styled.</p>\n</li>\n<li>\n<p>Style your page using CSS.</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>","headings":[{"depth":3,"value":"Day 1"},{"depth":4,"value":"1. Introduction"},{"depth":4,"value":"2. Scope and Operators"},{"depth":3,"value":"Day 2"},{"depth":4,"value":"3. Document Object Model"},{"depth":4,"value":"4. Decision Making"},{"depth":3,"value":"Day 3"},{"depth":4,"value":"5. Object-Oriented Programming in JavaScript"},{"depth":4,"value":"6. Introduction to jQuery"},{"depth":3,"value":"Day 4"},{"depth":4,"value":"7. AJAX"},{"depth":4,"value":"8. Introduction to Flexbox"},{"depth":3,"value":"Day 5"},{"depth":4,"value":"9. Introduction to Bootstrap"},{"depth":4,"value":"10. Final Project"},{"depth":5,"value":"Personal Website"}],"frontmatter":{"title":"Front End Foundations Syllabus"}}},"pathContext":{"slug":"/courses/frontend-foundations/syllabus/"}}

